

















































































import { Component, Prop, Vue } from "vue-property-decorator"
import {
  TextInputType,
  textInputTypes
} from "@/components/form/types/TextInputType.ts"

@Component({
  inheritAttrs: false
})
export default class TextInput extends Vue {
  @Prop({ required: true }) title!: string
  @Prop() type?: string
  @Prop() name?: string
  @Prop() value?: string
  @Prop() placeholder?: string
  @Prop() isrequired?: boolean

  isTextInputType(type: string | TextInputType): type is TextInputType {
    return (textInputTypes as readonly string[]).includes(type)
  }

  get fieldType(): TextInputType {
    if (!this.type) return "text"

    if (this.isTextInputType(this.type)) return this.type

    return "text"
  }

  get fieldRequired(): boolean {
    if (!this.isrequired) return false

    return true
  }

  get fieldPlaceholder(): string {
    if (!this.placeholder) return ""

    return this.placeholder
  }
}
