






































































































































































































import { Component, Vue } from "vue-property-decorator"
import SVGIcon from "@/components/common/SVGIcon.vue"

@Component({
  components: {
    SVGIcon
  }
})
export default class Nav extends Vue {
  // Initialized with the current scroll pos
  // so that it can start out in the .fixed state on reload
  scrollPosition = window.scrollY

  menuActive = false

  navLinks = [
    {
      title: "Home",
      to: "/",
      icon: "home"
    },
    {
      title: "Posts",
      to: "/posts",
      icon: "megaphone"
    },
    {
      title: "About us",
      to: "/about",
      icon: "information"
    },
    {
      title: "Games",
      to: "/games",
      icon: "gameController"
    }
  ]

  updateScroll() {
    this.scrollPosition = window.scrollY
  }

  toggleNavMenu() {
    this.menuActive = !this.menuActive
  }

  created() {
    window.addEventListener("scroll", this.updateScroll)
  }
}
