

























































import { Prop, Component, Vue } from "vue-property-decorator"
import SVGIcon from "@/components/common/SVGIcon.vue"

@Component({
  components: {
    SVGIcon
  }
})
export default class Button extends Vue {
  @Prop({ required: true }) title!: string
  @Prop() icon?: string
  @Prop() iconside!: "left" | "right"

  get iconSide(): "left" | "right" {
    if (!this.iconside) return "left"

    return this.iconside
  }

  iconIsRequested(): boolean {
    if (!this.icon) return false

    return true
  }

  useLeftIcon(): boolean {
    if (this.iconIsRequested() && this.iconSide === "left") return true

    return false
  }

  useRightIcon(): boolean {
    if (this.iconIsRequested() && this.iconSide === "right") return true

    return false
  }
}
